        display: grid;
        grid-template-columns: 100%;
    }
}

@media screen and (max-width: 800px) {

    .quillResponsiveCelular {
        padding-bottom: 100px;
    }
}

.fondoEncuestas {
    background: rgba(238, 244, 255, 1);
    min-height: calc(100vh - 50px);
    /*background: linear-gradient(90deg, rgba(208,223,255,1) 0%, rgba(238,244,255,1) 50%, rgba(208,223,255,1) 100%);*/
}

.estiloForm {
    background: white;
    border-radius: 25px;
    margin-right: 50PX;
    margin-left: 50px;
}

.botones {
    min-height: 40px;
    color: white;
    min-width: 110px;
}

.cardPersonalizado {
    border: solid 1px rgb(184, 184, 184);
    border-radius: 20px;
    box-shadow: 5px 5px 10px rgb(176, 196, 233);
    transition: box-shadow 0.3s;
    margin-top: 35px;
}

.cardPersonalizado:hover {
    box-shadow: 5px 5px 15px rgb(152, 176, 219);
}

.cardInside {
    padding: 15px;
}

.estiloRadios {
    margin-right: 10px;
}

.labelGen {
    color: #000000;
}

.error-message {
    color: red;
    display: none;
    /* Inicialmente oculto */
}

input:valid+.error-message {
    display: block;
    /* Mostrar el mensaje de error cuando el input sea inválido */
}
</style>